  /*------Header middle sub-menu  style--------*/
  .menu-wrapper ul li{
    position: relative;
  }
  .menu-wrapper .sub-menu {
    position: absolute;
    top: 120px;
    left: 0px;
    width: 270px;
    background: #fff;
    transition: .5s;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
    padding: 25px 0px 25px 5px;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    border-radius: 5px;
  }
  .menu-wrapper ul li:hover .sub-menu {
    top: 87px;
    visibility: visible;
    opacity: 1;
    z-index: 999;
  }
  .menu-wrapper .sub-menu li {
    position: relative;
    line-height: 2;
    display: block;
    text-align: left;
    transition: .3s;
    cursor: pointer;
  }
  .menu-wrapper .sub-menu li a::after{
    content:"";
    position: absolute;
    left: 0px;
    top: 50%;
    height: 1px;
    width: 15px;
    background: #7744D9;
    visibility: hidden;
    opacity: 0;
    transition:.3s;
  }
  .menu-wrapper .sub-menu li:hover a {
    padding-left: 35px;
    color: #7744D9 !important;
  }
  .menu-wrapper .sub-menu li:hover a i{
    color: #7744D9 !important;
  }
  .menu-wrapper .sub-menu li:hover a::after{
    visibility: visible;
    position: absolute;
    left: 8px;
    opacity: 1;
  }
  .menu-wrapper .sub-menu li a {
    font-size: 16px;
    padding: 8px 15px;
    display: block;
    line-height: 1.3;
    transition: .3s;
    letter-spacing: normal;
    text-transform: capitalize;
    margin: 0 !important;
    color: #616161 !important;
  }
  .menu-wrapper .sub-menu li a i{
    color: #555 !important;
    background: #fff !important;
    transition: .3s;
  }
  
  /*------Inner sub-menu style--------*/
  .menu-wrapper .sub-menu .inner-sub-menu {
    position: absolute;
    left: 265px;
